.homepage {
  position: relative;
  &_photo {
    width: 50%;
    position: fixed;
    left: 50%;
    z-index: 5;
    &-logo {
      position: absolute;
      left: 1rem;
      top: 0rem;
      height: 9rem;
      background-color: $color-light;
      padding-right: 35vw;
    }
    &-img {
      max-height: 100vh;
      width: auto;
      max-width: 100%;
      background-size: cover;
    }
  }
  &_menu {
    display: flex;
    position: fixed;
    width: 100%;
    justify-content: flex-end;
    align-items: flex-start;
    padding-top: 1.2rem;
    padding-bottom: 3rem;
    z-index: 10;
    font-family: $font-2;
    font-weight: 400;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 10rem;
      width: 50%;
      background-color: $color-light;
    }
    &_list {
      list-style: none;
      display: flex;
      align-items: flex-end;
      padding-top: 3rem;
      padding-right: 3rem;
      position: relative;
      z-index: 15;
      &_item {
        margin-right: 2rem;
        font-size: 1.2rem;
        letter-spacing: 0.2rem;
        position: relative;
        cursor: pointer;
        transition: all $trans-primary;
        color: $color-secondary;
        font-weight: 700;
        &::before {
          content: '';
          position: absolute;
          top: 60%;
          left: 0;
          right: 0;
          height: 4px;
          background-color: $color-secondary;
          transform-origin: center right;
          transform: scaleX(0) translateY(-50%);
          transition: transform 0.3s ease;
        }
        &:hover::before {
          transform-origin: center left;
          transform: scaleX(1) translateY(-50%);
        }
      }
    }
  }
}

.aboveFold {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  background-color: $color-light;
  position: relative;
  height: 100vh;
  &_card {
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    font-size: 3rem;
    width: 50%;
    color: $color-secondary;
    &_content {
      height: 100%;
      padding-left: 2.5rem;
      padding-top: 10rem;
    }
    &_subscribe {
      display: flex;
      flex-direction: column;
      padding-left: 2.5rem;
      &_input {
        color: $color-secondary;
        font-size: 1.5rem;
        background-color: $color-light;
        outline: none;
        border: none;
      }
      &_rect {
        height: 0.2rem;
        background-color: $color-secondary;
      }
      &_send {
        display: flex;
        align-items: center;
        justify-content: space-between;
        &_info {
          font-size: 0.7rem;
          margin-left: 0.5rem;
          &-underline {
            cursor: pointer;
            text-decoration: underline;
          }
        }
        &_button {
          background-color: $color-secondary;
          width: 8rem;
          margin-left: 2rem;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          color: $color-light;
          p {
            font-size: 1rem;
            color: $color-light;
            opacity: 0.9;
            margin-right: 1rem;
          }
        }
      }
    }
  }
  &_photo {
    height: 100%;
    width: 50%;
    background-image: url('../images/gallery.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: right;
  }
}

// change size of mail icon HERE
.mailicon {
  height: 1rem;
  width: auto;
  opacity: 0.9;
}
